import * as React from "react";
import { Link } from "react-router-dom";

export default class AdventureList extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      openToursCollection: [
        {
          slug: "althrif",
          name: "Alþrif",
          thumbnail: { url: "/img/pexels-karolina-grabowska-4870671.jpg" },
          duration: "2 klst",
          type: "",
          text: "Bíllinn þrifinn að innan og utan",
          price: "40.000 kr.",
          message: "Vinsælast"
        },
        {
          slug: "innan",
          name: "Þrif að innan",
          thumbnail: { url: "/img/pexels-tima-miroshnichenko-6873015.jpg" },
          duration: "1 klst",
          type: "",
          text: "Teppi og sæti ryksuguð og fletir þrifnir með tusku ",
          price: "20.000 kr.",
          message: "Vinsælast"
        },
        {
          slug: "utan",
          name: "Þrif að utan",
          thumbnail: { url: "/img/pexels-tima-miroshnichenko-6872601.jpg" },
          duration: "1 klst",
          type: "",
          text: "Skolaður með tjöruleysir og sápu, svo varinn með bóni",
          price: "20.000 kr.",
          message: "Vinsælast"
        },
      ],
    };
  }

  render() {
    return (
      <div
        id="adventures"
        className="flex flex-col w-full max-w-[1408px] py-16 px-4 laptop:px-16"
      >
      <h1 className="text-3xl text-center mb-12">Þetta er það sem við <strong><i>sérhæfum</i></strong> okkur í.</h1>
        <div className="flex flex-col gap-8">
          <ul className="grid grid-cols-1 laptop:grid-cols-2 desktop:grid-cols-3 gap-8">
            {this.state.openToursCollection.length > 0 ? (
                this.state.openToursCollection.map((e) => (
                    <Link>
                      <li className="flex flex-col w-full aspect-[3/4] group text-white rounded-lg overflow-hidden bg-black">
                        <div className="h-full relative flex flex-col items-center">
                          <div className="relative h-full bg-black">
                            <img
                              src={e.thumbnail.url}
                              className="z-20 w-full h-full object-cover group-hover:brightness-110 bg-black"
                            />
                            <img
                              className="w-full h-full absolute top-0 bg-gradient-to-t from-black/50 via-black/0 to-black/0"
                            />
                          </div>
                          <div className="absolute bottom-0 m-0 p-0 w-full">
                            <div className="relative z-20 p-8 flex flex-col items-center leading-none">
                              <span className="w-full text-center text-xl drop-shadow-[0_0_1px_rgba(0,0,0,1)]">{e.duration}</span>
                              <span className="w-full text-center tracking-wide font-['norse'] text-5xl drop-shadow-[0_0_1px_rgba(0,0,0,1)]">{e.name}</span>
                              <span className="w-full text-center text-2xl drop-shadow-[0_0_1px_rgba(0,0,0,1)]">{e.text}</span>
                              <span className="w-full text-center text-2xl mt-1 tracking-wide italic drop-shadow-[0_0_1px_rgba(0,0,0,1)]">{e.price}</span>
                            </div>
                            <div className="absolute z-10 bottom-0 left-0 right-0 top-0 w-full h-full bg-gradient-to-t from-black to-transparent"></div>
                          </div>
                        </div>
                      </li>
                    </Link>
                  ))
              ) : (
                <span className="text-4xl col-span-full text-center">
                  No Open Tours Found
                </span>
              )}
          </ul>
        </div>

      </div>
    );
  }
}
