import * as React from "react";
import { Link } from "react-router-dom";
import { HashLink } from "react-router-hash-link";

import AdventureList from "../components/AdventureList";
import Footer from "../components/Footer";
import Reviews from "../components/Reviews";
import Subscribe from "../components/Subscribe";

import ReactGA from 'react-ga4';
import { Modal } from "rsuite";
import 'rsuite/dist/rsuite-no-reset.min.css';

export default class Home extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      navActive: false,
      screenWidth: window.innerWidth,
      modalOpen: false,
    };

    this.hideNav = this.hideNav.bind(this);
    this.showNav = this.showNav.bind(this);

    this.handleOpen = () => this.setState({ modalOpen: true });
    this.handleClose = () => this.setState({ modalOpen: false });
  }

  hideNav() {
    this.setState({ navActive: false });
  }

  showNav() {
    this.setState({ navActive: true });
    window.scrollTo(0, 0);
  }

  componentDidMount() {
    if (this.state.navActive && window.innerWidth < 1440) {
      document.body.classList.add("overflow-hidden");
    } else document.body.classList.remove("overflow-hidden");

    window.addEventListener("resize", () => {
      this.setState({
        screenWidth: window.innerWidth,
      });
      if (this.state.navActive && window.innerWidth < 1440) {
        document.body.classList.add("overflow-hidden");
      } else {
        document.body.classList.remove("overflow-hidden");
        this.hideNav();
      }
    });

    document.title = "Hreinn bíll í sumar | Mosó Bón";

    ReactGA.initialize('G-CZG7EJ3P8E');
    ReactGA.send({ hitType: "pageview", page: window.location.pathname });
  }

  componentDidUpdate() {
    if (this.state.navActive && window.innerWidth < 1440) {
      document.body.classList.add("overflow-hidden");
    } else document.body.classList.remove("overflow-hidden");
  }

  render() {
    return (
      <div className={"App flex flex-col items-center"}>

        <header
          className={
            "flex flex-col items-center desktop:justify-center w-full group overflow-hidden" +
            (this.state.navActive & (this.state.screenWidth < 1440)
              ? " absolute"
              : " h-screen relative")
          }
        >

          {this.state.navActive && (
            <button
              onClick={this.hideNav}
              className="text-3xl p-8 absolute top-0 right-0 text-white z-50"
            >
              ☰
            </button>
          )}

          <img
            src="/img/pexels-autorecords-11191202.jpg"
            alt="A volcanic eruption"
            className="w-full h-full object-cover bg-black"
          />
          <div className="absolute z-10 bottom-0 left-0 right-0 top-0 w-full h-full bg-gradient-to-t from-black to-transparent"></div>
          <div className="absolute z-20 h-full w-full flex flex-col items-start justify-end p-4 laptop:p-16 max-w-[1408px]">
            <div className="flex flex-col gap-4 mb-16">
              <span className="text-4xl tablet:text-5xl laptop:text-6xl laptop-lg:text-7xl desktop:text-8xl text-white font-black drop-shadow-lg uppercase">
                Mosó Bón
              </span>
              <div className="flex flex-col tablet:flex-row gap-4 text-xl desktop:text-3xl text-black font-black">
                <a
                  className="bg-neutral-100 hover:bg-neutral-200 p-4 rounded-lg cursor-pointer"
                  onClick={() => this.handleOpen()}
                >
                  Bóka Tíma
                </a>
              </div>
            </div>
          </div>
        </header>
        <AdventureList />
        <Footer />
        
        <Modal size="lg" open={this.state.modalOpen} onClose={this.handleClose}>
          <Modal.Header></Modal.Header>
          <Modal.Body>
            <div>
              <iframe src="https://noona.is/mosobon/book" className="w-full h-full min-h-screen" height="100%" />
            </div>
          </Modal.Body>
        </Modal>
      </div>
    );
  }
}
